.wp-block[data-align="center"] > .wp-block-popup-maker-cta-button {
	text-align: center;
	margin-left: auto;
	margin-right: auto;
}

.wp-block[data-align="right"] > .wp-block-popup-maker-cta-button {
	/*!rtl:ignore*/
	text-align: right;
}

.wp-block-popup-maker-cta-button {
	position: relative;
	cursor: text;

	// Add outline to button on focus to indicate focus-state
	&:focus {
		box-shadow:
			0 0 0 1px #fff,
			0 0 0 3px var(--wp-admin-theme-color);

		// Windows' High Contrast mode will show this outline, but not the box-shadow.
		outline: 2px solid transparent;
		outline-offset: -2px;
	}

	// Increase placeholder opacity to meet contrast ratios.
	&[data-rich-text-placeholder]::after {
		opacity: 0.8;
	}
}

// Display "table" is used because the button container should only wrap the content and not takes the full width.
div[data-type="popup-maker/cta-button"] {
	display: table;
}

.pum-cta-error-notice {
	margin-bottom: 10px;
}
